body {
  margin: 0;
  background-color: black;
  user-select: none;
}


nav {
  background-color: black;
  color: aliceblue;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}



.note-input {
  text-align: center;
  background-color: black;
}


textarea {
  width: 50%;
  height: 50%;
  border-radius: 10px;
  border: 5px solid green;
  font-family: poppins;
}

@media only screen and (max-width:500px) {
  textarea {
    background-color: #ffdfc3;
    width: 95%;
    height: 75%;
    font-size: 25px;
    font-family: poppins;

  }
}

.add-note {
  padding: 15px 25px;
  font-size: 24px;
  text-align: center;
  cursor: pointer;
  outline: none;
  color: #fff;
  background-color: #06f727;
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px #999;
  margin: 20px 0px;
}


.add-note:active {
  background-color: #03a809;
  box-shadow: 0 5px #666;
  transform: translateY(5px);
}

@media only screen and (max-width:500px) {
  .add-note {
    height: 50px;
    width: 200px;
    font-size: 20px;
  }
}

.ul {
  list-style: none;
  justify-content: space-around;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;

}

@media only screen and (max-width:500px) {
  .ul {
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
  }
}

.btn {
  background-color: transparent;
  cursor: pointer;
  border-radius: 20%;
  border: transparent;
  margin: 0 5px;
}

.btn-container {
  background-color: white;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-55%, 110%);
  display: flex;
  height: 50px;

}



.ListItem {
  position: relative;
  background-color: #ffdfc3;
  padding: 15px;
  width: 200px;
  margin: 5px;
  overflow: hidden;
  border-radius: 25px;
  height: 250px;
}

@media only screen and (max-width:500px) {
  .ListItem {
    width: 90%;
    height: 250px;
    transform: translate(-8%, 0%)
  }
}

.note-list {
  text-align: center;
  /* background-image: url(https://images.pexels.com/photos/612252/pexels-photo-612252.jpeg?auto=compress&cs=tinysrgb&w=600);
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: -3%; */
}

@media only screen and (max-width:500px) {
  .note-list {
    margin: 0 10%;

  }
}

.note-preview {
  background-color: aliceblue;
  border: 2px solid;
  text-align: center;
  position: absolute;
  top: 44%;
  left: 50%;
  transform: translate(-50%, -60%);
  padding: 20px;
  font-family: poppins;

  width: 50%;
  height: fit-content;
  border-radius: 21px;

}


.close {
  position: absolute;
  top: 10%;
  left: 97%;
  transform: translate(-55%, -50%);
  cursor: pointer;
}

@media only screen and (max-width:500px) {
  .note-preview {
    height: 80%;
    width: 80%;
    font-size: 20px;
    transform: translate(-50%, -41%);
    overflow-y: scroll;
  }

  .close {
    top: 3%;
    left: 93%;
    width: 10%;
    height: 5%;
    border-radius: 20%;
    transform: translate(-34%, 48%);
    position: sticky;
  }
}

.btn-container:hover {
  color: rgb(255, 9, 193);
  transition: 2s;

  border: 2px solid green;
  text-decoration: underline;
}

.ListItem:hover {
  background-color: rgba(168, 255, 178, 0.747);
  border: 2px solid rgb(255, 255, 255);
  transition: ease-in-out 2s;
}

h3 {
  font-family: poppins;
  font-size: 22px;
}

h2 {
  color: aliceblue;

  text-align: center;
}

p {
  text-align: justify;
  padding: 10;
  margin: 4px;
  font-family: poppins;

}

@media only screen and (max-width:500px) {
  .preview-p {
    text-align: justify;
    margin-top: -75px;
    font-family: poppins;
  }
}